'use client';

import { useState, useMemo } from 'react';
import { useRouter } from 'next/navigation';

type CategoryFilter = 'all' | 'nutrients' | 'supplements' | 'ph_adjusters' | 'bundles';
type StatusFilter = 'all' | 'in_stock' | 'low_stock' | 'out_of_stock';
type LocationFilter = 'all' | 'texas_wh' | 'amazon_fba' | 'amazon_fbm';

interface ProductData {
  id: string;
  name: string;
  category: 'nutrients' | 'supplements' | 'ph_adjusters' | 'bundles';
  weight: string;
  skus: {
    internal: string;
    shopify: string;
    amazon: string;
  };
  stock: {
    total: number;
    texas: number;
    fba: number;
  };
  cost: {
    rolling: number;
    fixed: number;
  };
  prices: {
    msrp: number;
    shopify: number;
    amazon: number;
    wholesale: number;
  };
}

// Mock product data
const mockProducts: ProductData[] = [
  {
    id: 'prod-1',
    name: '1L Micro Classic',
    category: 'nutrients',
    weight: '2.8 lbs',
    skus: { internal: 'CLM1L', shopify: 'MICRO-1L-CLASSIC', amazon: 'CL-MICRO-1L' },
    stock: { total: 24, texas: 18, fba: 6 },
    cost: { rolling: 6.56, fixed: 6.12 },
    prices: { msrp: 24.99, shopify: 22.99, amazon: 23.49, wholesale: 14.99 },
  },
  {
    id: 'prod-2',
    name: '1L Bloom Classic',
    category: 'nutrients',
    weight: '2.9 lbs',
    skus: { internal: 'CLB1L', shopify: 'BLOOM-1L-CLASSIC', amazon: 'CL-BLOOM-1L' },
    stock: { total: 342, texas: 280, fba: 62 },
    cost: { rolling: 6.78, fixed: 6.45 },
    prices: { msrp: 24.99, shopify: 22.99, amazon: 23.49, wholesale: 14.99 },
  },
  {
    id: 'prod-3',
    name: '500ml CalMag Plus',
    category: 'supplements',
    weight: '1.4 lbs',
    skus: { internal: 'CALMAG500', shopify: 'CALMAG-500ML', amazon: 'CN-CALMAG-500' },
    stock: { total: 156, texas: 120, fba: 36 },
    cost: { rolling: 4.23, fixed: 4.10 },
    prices: { msrp: 18.99, shopify: 16.99, amazon: 17.49, wholesale: 11.99 },
  },
  {
    id: 'prod-4',
    name: 'pH Down 1L',
    category: 'ph_adjusters',
    weight: '2.4 lbs',
    skus: { internal: 'PHD1L', shopify: 'PH-DOWN-1L', amazon: 'CN-PHD-1L' },
    stock: { total: 0, texas: 0, fba: 0 },
    cost: { rolling: 3.45, fixed: 3.20 },
    prices: { msrp: 14.99, shopify: 12.99, amazon: 13.49, wholesale: 8.99 },
  },
  {
    id: 'prod-5',
    name: 'Starter Bundle Classic',
    category: 'bundles',
    weight: '8.5 lbs',
    skus: { internal: 'SBTCLASSIC', shopify: 'STARTER-BUNDLE-CL', amazon: 'CN-STARTER-CL' },
    stock: { total: 45, texas: 45, fba: 0 },
    cost: { rolling: 18.90, fixed: 17.50 },
    prices: { msrp: 64.99, shopify: 59.99, amazon: 62.99, wholesale: 44.99 },
  },
  {
    id: 'prod-6',
    name: '1L Grow Classic',
    category: 'nutrients',
    weight: '2.8 lbs',
    skus: { internal: 'CLG1L', shopify: 'GROW-1L-CLASSIC', amazon: 'CL-GROW-1L' },
    stock: { total: 18, texas: 12, fba: 6 },
    cost: { rolling: 6.34, fixed: 6.00 },
    prices: { msrp: 24.99, shopify: 22.99, amazon: 23.49, wholesale: 14.99 },
  },
  {
    id: 'prod-7',
    name: 'Root Booster 500ml',
    category: 'supplements',
    weight: '1.3 lbs',
    skus: { internal: 'RB500', shopify: 'ROOT-BOOST-500', amazon: 'CN-RB-500' },
    stock: { total: 89, texas: 65, fba: 24 },
    cost: { rolling: 5.12, fixed: 4.85 },
    prices: { msrp: 21.99, shopify: 19.99, amazon: 20.49, wholesale: 13.99 },
  },
  {
    id: 'prod-8',
    name: 'pH Up 1L',
    category: 'ph_adjusters',
    weight: '2.4 lbs',
    skus: { internal: 'PHU1L', shopify: 'PH-UP-1L', amazon: 'CN-PHU-1L' },
    stock: { total: 67, texas: 50, fba: 17 },
    cost: { rolling: 3.55, fixed: 3.30 },
    prices: { msrp: 14.99, shopify: 12.99, amazon: 13.49, wholesale: 8.99 },
  },
];

export default function InventoryPage() {
  const router = useRouter();
  const [searchQuery, setSearchQuery] = useState('');
  const [categoryFilter, setCategoryFilter] = useState<CategoryFilter>('all');
  const [statusFilter, setStatusFilter] = useState<StatusFilter>('all');
  const [locationFilter, setLocationFilter] = useState<LocationFilter>('all');
  const [selectedProducts, setSelectedProducts] = useState<Set<string>>(new Set());
  const [currentPage, setCurrentPage] = useState(1);
  const [viewMode, setViewMode] = useState<'list' | 'grid'>('list');
  const itemsPerPage = 6;

  // Get stock status
  const getStockStatus = (product: ProductData) => {
    if (product.stock.total === 0) return 'out_of_stock';
    if (product.stock.total < 30) return 'low_stock';
    return 'in_stock';
  };

  // Calculate margin
  const calculateMargin = (price: number, cost: number) => {
    if (price === 0) return 0;
    return ((price - cost) / price) * 100;
  };

  // Calculate average margin across all price points
  const getAvgMargin = (product: ProductData) => {
    const margins = [
      calculateMargin(product.prices.msrp, product.cost.rolling),
      calculateMargin(product.prices.shopify, product.cost.rolling),
      calculateMargin(product.prices.amazon, product.cost.rolling),
      calculateMargin(product.prices.wholesale, product.cost.rolling),
    ];
    return margins.reduce((a, b) => a + b, 0) / margins.length;
  };

  // Filter products
  const filteredProducts = useMemo(() => {
    return mockProducts.filter((product) => {
      // Search filter
      if (searchQuery) {
        const query = searchQuery.toLowerCase();
        if (
          !product.name.toLowerCase().includes(query) &&
          !product.skus.internal.toLowerCase().includes(query) &&
          !product.skus.shopify.toLowerCase().includes(query) &&
          !product.skus.amazon.toLowerCase().includes(query)
        ) {
          return false;
        }
      }

      // Category filter
      if (categoryFilter !== 'all' && product.category !== categoryFilter) {
        return false;
      }

      // Status filter
      const status = getStockStatus(product);
      if (statusFilter !== 'all' && status !== statusFilter) {
        return false;
      }

      return true;
    });
  }, [searchQuery, categoryFilter, statusFilter]);

  // Pagination
  const totalPages = Math.ceil(filteredProducts.length / itemsPerPage);
  const paginatedProducts = filteredProducts.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  // Calculate stats
  const totalProducts = mockProducts.length;
  const totalUnits = mockProducts.reduce((sum, p) => sum + p.stock.total, 0);
  const inventoryValue = mockProducts.reduce((sum, p) => sum + (p.stock.total * p.prices.msrp), 0);
  const costOnHand = mockProducts.reduce((sum, p) => sum + (p.stock.total * p.cost.rolling), 0);
  const overallMargin = inventoryValue > 0 ? ((inventoryValue - costOnHand) / inventoryValue) * 100 : 0;
  const lowStockCount = mockProducts.filter(p => {
    const status = getStockStatus(p);
    return status === 'low_stock';
  }).length;
  const outOfStockCount = mockProducts.filter(p => p.stock.total === 0).length;

  // Category styles
  const getCategoryStyle = (category: ProductData['category']) => {
    const styles = {
      nutrients: {
        gradient: 'from-emerald-500/20 to-emerald-600/20',
        border: 'border-emerald-500/20',
        text: 'text-emerald-400',
        icon: 'fa-flask',
      },
      supplements: {
        gradient: 'from-purple-500/20 to-purple-600/20',
        border: 'border-purple-500/20',
        text: 'text-purple-400',
        icon: 'fa-seedling',
      },
      ph_adjusters: {
        gradient: 'from-blue-500/20 to-blue-600/20',
        border: 'border-blue-500/20',
        text: 'text-blue-400',
        icon: 'fa-vial',
      },
      bundles: {
        gradient: 'from-amber-500/20 to-amber-600/20',
        border: 'border-amber-500/20',
        text: 'text-amber-400',
        icon: 'fa-boxes-stacked',
      },
    };
    return styles[category];
  };

  const getCategoryLabel = (category: ProductData['category']) => {
    const labels = {
      nutrients: 'Nutrients',
      supplements: 'Supplements',
      ph_adjusters: 'pH Adjusters',
      bundles: 'Bundles',
    };
    return labels[category];
  };

  // Handle row click
  const handleRowClick = (productId: string) => {
    router.push(`/inventory/${productId}`);
  };

  // Handle checkbox
  const handleCheckbox = (productId: string, e: React.MouseEvent) => {
    e.stopPropagation();
    const newSelected = new Set(selectedProducts);
    if (newSelected.has(productId)) {
      newSelected.delete(productId);
    } else {
      newSelected.add(productId);
    }
    setSelectedProducts(newSelected);
  };

  // Handle select all
  const handleSelectAll = () => {
    if (selectedProducts.size === paginatedProducts.length) {
      setSelectedProducts(new Set());
    } else {
      setSelectedProducts(new Set(paginatedProducts.map(p => p.id)));
    }
  };

  // Format currency
  const formatCurrency = (value: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 2,
    }).format(value);
  };

  const formatNumber = (value: number) => {
    return new Intl.NumberFormat('en-US').format(value);
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-white">Inventory</h1>
          <p className="text-sm text-slate-400">Manage products, pricing, and stock levels</p>
        </div>
        <div className="flex items-center gap-3">
          <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors flex items-center gap-2">
            <i className="fas fa-download text-sm"></i>
            Export
          </button>
          <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-300 rounded-lg transition-colors flex items-center gap-2">
            <i className="fas fa-upload text-sm"></i>
            Import
          </button>
          <button className="px-4 py-2 bg-emerald-600 hover:bg-emerald-500 text-white rounded-lg transition-colors flex items-center gap-2">
            <i className="fas fa-plus text-sm"></i>
            Add Product
          </button>
        </div>
      </div>

      {/* Stats Row */}
      <div className="grid grid-cols-5 gap-4">
        {/* Total Products */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="text-sm text-slate-400 mb-1">Total Products</div>
          <div className="text-2xl font-bold text-white">{totalProducts}</div>
          <div className="text-xs text-slate-500 mt-1">Active SKUs</div>
        </div>

        {/* Total Units */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="text-sm text-slate-400 mb-1">Total Units</div>
          <div className="text-2xl font-bold text-white">{formatNumber(totalUnits)}</div>
          <div className="text-xs text-emerald-400 mt-1">
            <i className="fas fa-arrow-up mr-1"></i>
            12% this month
          </div>
        </div>

        {/* Inventory Value */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="text-sm text-slate-400 mb-1">Inventory Value</div>
          <div className="text-2xl font-bold text-white">{formatCurrency(inventoryValue)}</div>
          <div className="text-xs text-slate-500 mt-1">At MSRP</div>
        </div>

        {/* Cost on Hand */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="text-sm text-slate-400 mb-1">Cost on Hand</div>
          <div className="text-2xl font-bold text-emerald-400">{formatCurrency(costOnHand)}</div>
          <div className="text-xs text-slate-500 mt-1">{overallMargin.toFixed(1)}% margin</div>
        </div>

        {/* Low Stock Items */}
        <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
          <div className="text-sm text-slate-400 mb-1">Low Stock Items</div>
          <div className="text-2xl font-bold text-amber-400">{lowStockCount}</div>
          <div className="text-xs text-red-400 mt-1">{outOfStockCount} out of stock</div>
        </div>
      </div>

      {/* Filters Row */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
        <div className="flex items-center gap-4">
          {/* Search */}
          <div className="flex-1 relative">
            <i className="fas fa-search absolute left-3 top-1/2 -translate-y-1/2 text-slate-400"></i>
            <input
              type="text"
              placeholder="Search by SKU, name, or barcode..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full bg-slate-800/50 border border-slate-700 rounded-lg pl-10 pr-4 py-2.5 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500/50 focus:ring-1 focus:ring-emerald-500/50"
            />
          </div>

          {/* Category Filter */}
          <select
            value={categoryFilter}
            onChange={(e) => setCategoryFilter(e.target.value as CategoryFilter)}
            className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50"
          >
            <option value="all">All Categories</option>
            <option value="nutrients">Nutrients</option>
            <option value="supplements">Supplements</option>
            <option value="ph_adjusters">pH Adjusters</option>
            <option value="bundles">Bundles</option>
          </select>

          {/* Location Filter */}
          <select
            value={locationFilter}
            onChange={(e) => setLocationFilter(e.target.value as LocationFilter)}
            className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50"
          >
            <option value="all">All Locations</option>
            <option value="texas_wh">Texas WH</option>
            <option value="amazon_fba">Amazon FBA</option>
            <option value="amazon_fbm">Amazon FBM</option>
          </select>

          {/* Stock Status Filter */}
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value as StatusFilter)}
            className="bg-slate-800/50 border border-slate-700 rounded-lg px-4 py-2.5 text-slate-300 focus:outline-none focus:border-emerald-500/50"
          >
            <option value="all">All Stock Levels</option>
            <option value="in_stock">In Stock</option>
            <option value="low_stock">Low Stock</option>
            <option value="out_of_stock">Out of Stock</option>
          </select>

          {/* View Toggle */}
          <div className="flex items-center bg-slate-800/50 border border-slate-700 rounded-lg p-1">
            <button
              onClick={() => setViewMode('list')}
              className={`px-3 py-1.5 rounded-md text-sm ${
                viewMode === 'list'
                  ? 'bg-emerald-500/20 text-emerald-400'
                  : 'text-slate-400 hover:text-white'
              }`}
            >
              <i className="fas fa-list"></i>
            </button>
            <button
              onClick={() => setViewMode('grid')}
              className={`px-3 py-1.5 rounded-md text-sm ${
                viewMode === 'grid'
                  ? 'bg-emerald-500/20 text-emerald-400'
                  : 'text-slate-400 hover:text-white'
              }`}
            >
              <i className="fas fa-grip"></i>
            </button>
          </div>
        </div>
      </div>

      {/* Inventory Table */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="bg-slate-800/50 border-b border-slate-700/50">
                <th className="px-4 py-3 text-left">
                  <input
                    type="checkbox"
                    checked={selectedProducts.size === paginatedProducts.length && paginatedProducts.length > 0}
                    onChange={handleSelectAll}
                    className="rounded bg-slate-700 border-slate-600"
                  />
                </th>
                <th className="px-4 py-3 text-left text-sm font-medium text-slate-400">Product</th>
                <th className="px-4 py-3 text-left text-sm font-medium text-slate-400">SKUs</th>
                <th className="px-4 py-3 text-center text-sm font-medium text-slate-400">Stock</th>
                <th className="px-4 py-3 text-right text-sm font-medium text-slate-400">Cost</th>
                <th className="px-4 py-3 text-right text-sm font-medium text-slate-400">MSRP</th>
                <th className="px-4 py-3 text-right text-sm font-medium text-slate-400">Shopify</th>
                <th className="px-4 py-3 text-right text-sm font-medium text-slate-400">Amazon</th>
                <th className="px-4 py-3 text-right text-sm font-medium text-slate-400">Wholesale</th>
                <th className="px-4 py-3 text-center text-sm font-medium text-slate-400">Avg Margin</th>
                <th className="px-4 py-3 text-center text-sm font-medium text-slate-400">Actions</th>
              </tr>
            </thead>
            <tbody>
              {paginatedProducts.map((product) => {
                const status = getStockStatus(product);
                const categoryStyle = getCategoryStyle(product.category);
                const avgMargin = getAvgMargin(product);

                return (
                  <tr
                    key={product.id}
                    onClick={() => handleRowClick(product.id)}
                    className="transition-colors cursor-pointer border-b border-slate-700/30 hover:bg-slate-800/30"
                  >
                    {/* Checkbox */}
                    <td className="px-4 py-4" onClick={(e) => handleCheckbox(product.id, e)}>
                      <input
                        type="checkbox"
                        checked={selectedProducts.has(product.id)}
                        onChange={() => {}}
                        className="rounded bg-slate-700 border-slate-600"
                      />
                    </td>

                    {/* Product */}
                    <td className="px-4 py-4">
                      <div className="flex items-center gap-3">
                        <div className={`w-12 h-12 bg-gradient-to-br ${categoryStyle.gradient} rounded-lg flex items-center justify-center border ${categoryStyle.border}`}>
                          <i className={`fas ${categoryStyle.icon} ${categoryStyle.text}`}></i>
                        </div>
                        <div>
                          <div className="font-medium text-white">{product.name}</div>
                          <div className="text-xs text-slate-400">{getCategoryLabel(product.category)} • {product.weight}</div>
                        </div>
                      </div>
                    </td>

                    {/* SKUs */}
                    <td className="px-4 py-4">
                      <div className="space-y-1">
                        <div className="flex items-center gap-2">
                          <span className="text-xs text-slate-500 w-16">Internal:</span>
                          <code className="text-xs bg-slate-800 px-2 py-0.5 rounded text-slate-300">{product.skus.internal}</code>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className="text-xs text-green-400 w-16"><i className="fab fa-shopify"></i></span>
                          <code className="text-xs bg-slate-800 px-2 py-0.5 rounded text-slate-300">{product.skus.shopify}</code>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className="text-xs text-orange-400 w-16"><i className="fab fa-amazon"></i></span>
                          <code className="text-xs bg-slate-800 px-2 py-0.5 rounded text-slate-300">{product.skus.amazon}</code>
                        </div>
                      </div>
                    </td>

                    {/* Stock */}
                    <td className="px-4 py-4">
                      <div className="text-center">
                        <div className={`text-lg font-bold ${
                          status === 'in_stock' ? 'text-emerald-400' :
                          status === 'low_stock' ? 'text-amber-400' : 'text-red-400'
                        }`}>
                          {product.stock.total}
                        </div>
                        <div className="text-xs text-slate-500 mt-1">
                          <span className="text-blue-400">{product.stock.texas}</span> TX •
                          <span className="text-orange-400"> {product.stock.fba}</span> FBA
                        </div>
                        <div className={`text-xs mt-1 ${
                          status === 'in_stock' ? 'text-emerald-400' :
                          status === 'low_stock' ? 'text-amber-400' : 'text-red-400'
                        }`}>
                          {status === 'in_stock' && '✓ In Stock'}
                          {status === 'low_stock' && '↓ Low Stock'}
                          {status === 'out_of_stock' && '✗ Out of Stock'}
                        </div>
                      </div>
                    </td>

                    {/* Cost */}
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm text-white">{formatCurrency(product.cost.rolling)}</div>
                      <div className="text-xs text-slate-500">Rolling Avg</div>
                      <div className="text-xs text-slate-400">Fixed: {formatCurrency(product.cost.fixed)}</div>
                    </td>

                    {/* MSRP */}
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm font-medium text-white">{formatCurrency(product.prices.msrp)}</div>
                      <div className="text-xs text-emerald-400">{calculateMargin(product.prices.msrp, product.cost.rolling).toFixed(1)}%</div>
                    </td>

                    {/* Shopify */}
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm text-white">{formatCurrency(product.prices.shopify)}</div>
                      <div className="text-xs text-emerald-400">{calculateMargin(product.prices.shopify, product.cost.rolling).toFixed(1)}%</div>
                    </td>

                    {/* Amazon */}
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm text-white">{formatCurrency(product.prices.amazon)}</div>
                      <div className="text-xs text-emerald-400">{calculateMargin(product.prices.amazon, product.cost.rolling).toFixed(1)}%</div>
                    </td>

                    {/* Wholesale */}
                    <td className="px-4 py-4 text-right">
                      <div className="text-sm text-white">{formatCurrency(product.prices.wholesale)}</div>
                      <div className="text-xs text-emerald-400">{calculateMargin(product.prices.wholesale, product.cost.rolling).toFixed(1)}%</div>
                    </td>

                    {/* Avg Margin */}
                    <td className="px-4 py-4 text-center">
                      <div className="inline-flex items-center gap-1 px-2 py-1 bg-emerald-500/10 text-emerald-400 rounded-full text-sm font-medium">
                        {avgMargin.toFixed(1)}%
                      </div>
                    </td>

                    {/* Actions */}
                    <td className="px-4 py-4" onClick={(e) => e.stopPropagation()}>
                      <div className="flex items-center justify-center gap-1">
                        <button
                          onClick={() => handleRowClick(product.id)}
                          className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors"
                          title="Edit"
                        >
                          <i className="fas fa-edit text-sm"></i>
                        </button>
                        <button
                          className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors"
                          title="Adjust Stock"
                        >
                          <i className="fas fa-boxes-stacked text-sm"></i>
                        </button>
                        <button
                          className="p-2 text-slate-400 hover:text-white hover:bg-slate-700 rounded-lg transition-colors"
                          title="More"
                        >
                          <i className="fas fa-ellipsis-v text-sm"></i>
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>

        {/* Empty State */}
        {filteredProducts.length === 0 && (
          <div className="py-12 text-center">
            <i className="fas fa-box-open text-4xl text-slate-600 mb-4"></i>
            <p className="text-sm text-slate-400">No products found</p>
          </div>
        )}

        {/* Pagination */}
        {filteredProducts.length > 0 && (
          <div className="px-5 py-4 border-t border-slate-700/50 flex items-center justify-between">
            <div className="text-sm text-slate-400">
              Showing <span className="text-white">{((currentPage - 1) * itemsPerPage) + 1}-{Math.min(currentPage * itemsPerPage, filteredProducts.length)}</span> of <span className="text-white">{filteredProducts.length}</span> products
            </div>
            <div className="flex items-center gap-2">
              <button
                onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                disabled={currentPage === 1}
                className="px-3 py-1.5 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400 rounded-lg transition-colors disabled:opacity-50"
              >
                <i className="fas fa-chevron-left text-sm"></i>
              </button>

              {Array.from({ length: Math.min(totalPages, 3) }, (_, i) => {
                const pageNum = i + 1;
                return (
                  <button
                    key={pageNum}
                    onClick={() => setCurrentPage(pageNum)}
                    className={`px-3 py-1.5 rounded-lg transition-colors ${
                      currentPage === pageNum
                        ? 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30'
                        : 'bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400'
                    }`}
                  >
                    {pageNum}
                  </button>
                );
              })}

              {totalPages > 3 && (
                <>
                  <span className="text-slate-500">...</span>
                  <button
                    onClick={() => setCurrentPage(totalPages)}
                    className={`px-3 py-1.5 rounded-lg transition-colors ${
                      currentPage === totalPages
                        ? 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30'
                        : 'bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400'
                    }`}
                  >
                    {totalPages}
                  </button>
                </>
              )}

              <button
                onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                disabled={currentPage === totalPages}
                className="px-3 py-1.5 bg-slate-800 hover:bg-slate-700 border border-slate-700 text-slate-400 rounded-lg transition-colors disabled:opacity-50"
              >
                <i className="fas fa-chevron-right text-sm"></i>
              </button>
            </div>
          </div>
        )}
      </div>

      {/* Legend */}
      <div className="bg-slate-800/50 backdrop-blur border border-slate-700/50 rounded-xl p-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-6 text-sm">
            <div className="flex items-center gap-2">
              <span className="text-slate-400">Stock Locations:</span>
              <span className="text-blue-400">TX = Texas WH</span>
              <span className="text-slate-500">•</span>
              <span className="text-orange-400">FBA = Amazon FBA</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="text-slate-400">Margin = Profit Margin (not markup)</span>
            </div>
          </div>
          <div className="flex items-center gap-4 text-sm">
            <div className="flex items-center gap-2">
              <span className="w-3 h-3 rounded bg-emerald-500/20 border border-emerald-500/30"></span>
              <span className="text-slate-400">In Stock</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="w-3 h-3 rounded bg-amber-500/20 border border-amber-500/30"></span>
              <span className="text-slate-400">Low Stock</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="w-3 h-3 rounded bg-red-500/20 border border-red-500/30"></span>
              <span className="text-slate-400">Out of Stock</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
